    -moz-animation: flicker 3s linear infinite;
    -webkit-animation: flicker 3s linear infinite;
  }
  .grid {
    border: 2px solid #333;
    border-radius: 3px;
    display: flex;
    flex-flow: row wrap;
    width: 700px;
  }
  .grid::after {
